.slider .slick-next:after {
  transform: translate(-50%, -50%) rotate(-90deg);
}

.slider .slick-next:hover:after {
  animation-name: jump-right;
  animation-duration: .4s;
  animation-iteration-count: 1;
}

.slide {
  position: relative;
  height: auto;
  width: 100%;
  border-radius: 20px;
  padding: 2rem 0;
  background: #fefefe;
}

@media print, screen and (min-width: 48em) {
  .slide {
    padding: 0;
    height: 24.5rem;
  }
}

.slide__inner {
  display: flex;
  align-items: center;
  height: 100%;
}

.slide__close {
  position: absolute;
  top: 1.5rem;
  right: 2rem;
}

.slide__close:hover {
  animation-name: jump-up;
  animation-duration: .4s;
  animation-iteration-count: 1;
}

.slide img {
  margin: 0 auto 1.5rem;
}

@media print, screen and (min-width: 48em) {
  .slide img {
    margin: 0;
  }
}

.footer {
  padding-top: 3rem;
}

@media print, screen and (min-width: 48em) {
  .footer {
    padding-top: 5rem;
  }
}

.footer__upper {
  text-align: center;
}

.footer__upper__inner {
  margin-bottom: 2rem;
}

.footer__upper__inner:last-child {
  margin-bottom: 0;
}

@media print, screen and (min-width: 48em) {
  .footer__upper__inner {
    margin-bottom: 0;
  }
}

.footer__lower {
  margin: 0 1rem;
  width: calc(100% - 2rem);
  height: auto;
  color: #fefefe;
  background: #7A8727;
  padding: 1.5rem 0;
  border-radius: 20px 20px 0 0;
}

@media print, screen and (min-width: 48em) {
  .footer__lower {
    padding: 0;
    height: 3.875rem;
  }
}

.footer__lower__headline {
  display: inline-block;
  margin-bottom: 0;
  margin-right: .5rem;
  transform: translateY(3px);
}

.footer__lower__marginalia {
  display: inline-block;
}

.footer__lower__marginalia a {
  color: #fefefe;
}

.footer__lower__grid {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
}

@media print, screen and (min-width: 48em) {
  .footer__lower__grid {
    justify-content: space-between;
    flex-direction: row;
  }
}

.footer__lower__left {
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  margin-bottom: 1rem;
}

@media print, screen and (min-width: 48em) {
  .footer__lower__left {
    margin-bottom: 0;
    text-align: left;
  }
}

.footer__nav-list {
  display: flex;
  flex-wrap: nowrap;
}

.footer__nav-item {
  margin-left: 1.5rem;
}

.footer__nav-link {
  position: relative;
  font-size: 12px;
  line-height: 16px;
  color: #fefefe;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
}

.footer__nav-link:after {
  content: '';
  position: absolute;
  bottom: -3px;
  left: 50%;
  transform: translateX(-50%);
  height: 2px;
  width: 0;
  border-radius: 2px;
  background: #fefefe;
  transition: width .2s ease;
}

.footer__nav-link.is-active:after, .footer__nav-link:hover:after {
  width: 1rem;
}
